import { favourite_list } from "./../../data/data";
import Image from "next/image";
export const CustomerFavoritesSection = () => (
  <section className="customer_fav_section">
    <h1>üî• Customer Favorites</h1>
    <div id="fav_list">
      {favourite_list.map((el, id) => {
        return (
          <ul className="customer_fav_card">
            <li key={id}>
              <div key={id} className="img_holder">
                <Image width={100} src={el.img} alt={`image_index_${id}`} />
              </div>
              {el.context}
            </li>
          </ul>
        );
      })}
    </div>
    <div className="order-prompt">
      <p>
        üìå Ready to Order? üí° Browse our full menu and satisfy your cravings
        today!
      </p>
      <a href="#">
        <button>View Full Menu</button>
      </a>
    </div>
  </section>
);
